.PreviewAnimation-enter,
.PreviewAnimationVertical-enter {
  opacity: 0;
  -webkit-transform: translate(-50px,0);
      -ms-transform: translate(-50px,0);
          transform: translate(-50px,0);
  -webkit-transition-property: opacity, -webkit-transform;
  transition-property: opacity, -webkit-transform;
  -o-transition-property: transform, opacity;
  transition-property: transform, opacity;
  transition-property: transform, opacity, -webkit-transform;
  -webkit-transition-duration: 300ms;
       -o-transition-duration: 300ms;
          transition-duration: 300ms;
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.665, 0.320, 1), linear;
       -o-transition-timing-function: cubic-bezier(0.175, 0.665, 0.320, 1), linear;
          transition-timing-function: cubic-bezier(0.175, 0.665, 0.320, 1), linear;
}

/*Mobile*/
.PreviewAnimationVertical-enter {
  -webkit-transform: translate(0,-50px);
      -ms-transform: translate(0,-50px);
          transform: translate(0,-50px);
}

.PreviewAnimation-enter.PreviewAnimation-enter-active,
.PreviewAnimationVertical-enter.PreviewAnimationVertical-enter-active {
  opacity: 1;
  -webkit-transform: translate(0,0);
      -ms-transform: translate(0,0);
          transform: translate(0,0);
}


.PreviewAnimation-leave,
.PreviewAnimationVertical-leave {
  opacity: 1;
  -webkit-transform: translate(0,0);
      -ms-transform: translate(0,0);
          transform: translate(0,0);
  -webkit-transition-property: opacity, -webkit-transform;
  transition-property: opacity, -webkit-transform;
  -o-transition-property: transform, opacity;
  transition-property: transform, opacity;
  transition-property: transform, opacity, -webkit-transform;
  -webkit-transition-duration: 230ms;
       -o-transition-duration: 230ms;
          transition-duration: 230ms;
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.665, 0.320, 1);
       -o-transition-timing-function: cubic-bezier(0.175, 0.665, 0.320, 1);
          transition-timing-function: cubic-bezier(0.175, 0.665, 0.320, 1); /*opacity ease out*/
}

.PreviewAnimation-leave.PreviewAnimation-leave-active,
.PreviewAnimationVertical-leave.PreviewAnimationVertical-leave-active {
  opacity: 0;
  -webkit-transform: translate(-35px,0);
      -ms-transform: translate(-35px,0);
          transform: translate(-35px,0);
}

/*Mobile*/
.PreviewAnimationVertical-leave.PreviewAnimationVertical-leave-active {
  -webkit-transform: translate(0,35px);
      -ms-transform: translate(0,35px);
          transform: translate(0,35px);
}



